/* -- payRent-- */
.payRent {
}
.pay_loading_overlay {
  z-index: 6;
  position: fixed; /* 将覆盖层固定在屏幕上 */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: "Open Sans";
  background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色背景 */
  display: flex; /* 使用 flexbox */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.pay_loading_spinner {
  width: 420px;
  height: 300px;
  background-color: #fff; /* 背景色可以根据需要调整 */
  border-radius: 10px; /* 可选，圆角 */
  font-size: 20px; /* 字体大小 */
  font-family: "Open Sans";
  color: #333; /* 字体颜色 */
  display: flex; /* 使用 flexbox */

  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* 阴影效果 */
  animation: fadeIn 0.3s ease-out; /* 弹窗淡入动画 */
}

.pay_loading_close {
  color: var(--clr-black); /* 设置文字颜色 */
  cursor: pointer; /* 鼠标悬浮变成指针 */
  text-decoration: none; /* 去掉下划线 */
  font-size: 25px;
  font-weight: 700;
}

.pay_loading_spinner button {
  width: 90px;
  height: 30px;
  margin: 10px 50px;
  font-family: "Open Sans", sans-serif;
  font-size: 14px; /* 字体 */
  font-weight: 600;
  color: #fff;
  background-color: var(--clr-blue);
  border: none;
  border-radius: 5px; /* 更圆润的边角 */
  cursor: pointer;
  transition: all 250ms ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* 按钮阴影效果 */
}
.pay_loading_spinner button:hover {
  background-color: var(--clr-blue-2); /* 按钮悬停效果 */
}

.pay_loading_spinner button:active {
  transform: scale(0.98); /* 按钮点击时缩放效果 */
}
.confilm_loading_message {
  width: 100%;
  padding: 20px 0px;
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
}
.pay_confilmMessage {
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.pay_confilmMessage_p {
  flex: 2;
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.pay_confilmMessage_p p {
  padding: 10px;
}
.pay_confilmMessage_button {
  flex: 1;
  bottom: 20px;
  margin: 10px 10px;
  display: flex; /* 使用 flexbox */
  flex-direction: row; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}

.pay_successMessage {
  width: 100%;
  padding: 30px 20px;
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.pay_successMessage_p {
  font-size: 17px;
  margin: 10px;
  flex: 2;
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.pay_successMessage_p p {
  padding: 10px 5px;
}
.pay_successMessage_button {
  flex: 1;
  display: flex; /* 使用 flexbox */
  flex-direction: row; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.loading_message {
  display: flex; /* 使用 flexbox */
  flex-direction: column; /* 水平排列子元素 */
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
